import React from "react";
import styled from "styled-components";


const PageContainer = styled.div`
  margin-top: 50px;
  margin-bottom: 50px;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
`

const Wrapper = styled.div `
    display: flex;
    width: 90%;
    max-width: 1000px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
`;

const HeaderButton = styled.button `
    background-color: transparent;
    background-repeat: no-repeat;
    border: none;
    cursor: pointer;
    overflow: hidden;
    outline: none;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-bottom: 2px solid gray;
    color: white;

    text-align: center;
    font-size: 24px;
    font-weight: bold;
`;

const DropdownBody = styled.div `
  overflow: hidden;
  max-height: ${props => props.showDropdown === true ? "10000px" : "0"};
  transform-origin: top;
  transition: max-height ${props => props.showDropdown === true ? "1s ease-in-out" : "0.5s cubic-bezier(0, 1, 0, 1);"};
  text-align: center;
  font-size: 16px;
  color: #e2e2e2;
  font-family: 'courier new';
  line-height: 16pt;
`;

const Symbol = styled.div`
  margin-left: 15px;
`;

const Purple = styled.span `
  color: rgb(153, 50, 204);
`;

const Red = styled.span `
  color: rgb(255, 0, 0);
`

const Green = styled.span `
  color: rgb(25, 255, 25);
`

const Quote = styled.p `
  background-color: rgb(23, 24, 29);
  padding-top: 10px;
  padding-bottom: 10px;
  border-left: 2px solid red;
`

const Chapter = styled.span `
  color: orange;
`

const Title = styled.span `
  font-size: 24px;
`

const Heading = (props) => {
  return (
    <HeaderButton onClick={props.onClick}>
      {props.text}
      <Symbol>{props.symbol}</Symbol>
    </HeaderButton>
  );
};

const Dropdown = (props) => {
  const [showDropdown, setShowDropdown] = React.useState(false);

  function ToggleDrowDown() {
    setShowDropdown(!showDropdown);
  }

  return (
    <Wrapper>
      <Heading text={props.title} symbol={showDropdown ? "˄" : "˅"} onClick={ () => ToggleDrowDown() }/>
      <DropdownBody showDropdown={showDropdown}>
        {props.content}
      </DropdownBody>
    </Wrapper>
  );
};

